<template>
  <div>
    <Skeleton v-if="loading" type="comments" />
    <div v-else-if="chats?.length">
      <div class="title">最近消息</div>
      <ul class="chat-list" v-infinite-scroll="loadMore" :infinite-scroll-disabled="!hasMorePages">
        <li v-for="chat in chats" :key="chat?.id" class="flex-row chat-item">
          <a :href="'/user/' + chat?.withUser?.id" class="avatar">
            <img :src="chat?.subject || chat?.withUser?.avatar" alt="avatar" class="user-avatar" />
          </a>
          <a :href="'/chat/' + chat?.withUser?.id" class="chat-info">
            <div class="flex-row">
              <a :href="'/chat/' + chat?.withUser?.id" class="user-name text-ellipsis">{{ chat?.withUser?.name }} </a>
              <div class="flex-row">
                <span v-if="chat?.unreads" class="badge">{{ unreads(chat?.unreads || 0) }}</span>
                <span class="meta"> {{ chat?.time_ago }}</span>
              </div>
            </div>
            <p class="last-message">
              {{ chat?.lastMessage?.message }}
            </p>
          </a>
        </li>
      </ul>
    </div>
    <Empty v-else type="content" description="暂无内容" />
    <LoadingSpinner v-if="loadingMore" />
  </div>
</template>

<script lang="ts">
import { ref } from 'vue';
import { useStore } from 'vuex';
import { useChatsQuery } from '@/graphql';
import moment from '@/common/moment';
import Skeleton from '@/components/Skeleton.vue';
import Empty from '@/components/Empty.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';

export default {
  name: 'Chats',
  components: {
    Skeleton,
    Empty,
    LoadingSpinner
  },
  setup() {
    const store = useStore();
    const user_id = ref(store.state?.user?.user?.id);

    const { chats, loading, loadMore, loadingMore, hasMorePages } = useChatsQuery(
      { user_id: user_id.value },
      { enabled: !!user_id.value }
    );

    return {
      chats,
      loading,
      loadMore,
      loadingMore,
      hasMorePages
    };
  },
  methods: {
    unreads(num) {
      return moment.transNumber(num);
    }
  }
};
</script>

